
import Calendar from 'vux/src/components/calendar/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import header from '../../components/header'
export default {
name: "loginpassword-item",
data(){
return{
sexChecked:false,
menus:['保密','男','女'], min:1950, max:2018,integral:'',
list:{ name:'', sex:'', birthday:'' }, sex:''
}
},
components: {
'header-item': header,
Calendar,Actionsheet,
Group,
XSwitch,
Toast,Datetime
},
directives: {
TransferDom
},
computed:{
user(){
if(this.$store.state.user == ''){
this.$store.commit('USER')
}
return this.$store.state.user.wtCustomer
},
},
created(){
document.title = '会员信息'

this.$nextTick(() => {
this.list.name = this.user.name
this.list.birthday = this.user.birthday != '' ? this.user.birthday.substring(0,10) : ''
this.list.sex = this.user.sex
this.sex = this.menus[this.user.sex]
})
},
methods: {
change (value) {
this.birthday = value
},
onChange (val) {

},
sexChange(){
this.sexChecked = !this.sexChecked
},
sex_click (key) {
this.sex = this.menus[key]
this.list.sex = key
},
Preservation(){
this.$store.dispatch('editUserInfo', this.list)
}
}
}



